




























import {Component, Vue, Prop} from "vue-property-decorator"
import { Md5 } from 'ts-md5/dist/md5';

@Component({
})
export default class App extends Vue {
    @Prop()
    commonData: any;

    message: string = '1234';


    intoArticle(item: any): void {


        // let md5ArticleRouter = Md5.hashStr(`whichArticle=${item.id}`)

        const {href} = this.$router.resolve({
            path: `article/whichArticle=${item.id}`
        })
        window.open(href)
    }
}
