

































import {Component, Vue} from "vue-property-decorator";
import {articleList} from '../../config/baseAxios'
import commonWidget from "../../components/common.vue";
import indexAsidePage from './indexAsidePage.vue'

@Component({
    components: {
        commonWidget,
        indexAsidePage,
    },
})
export default class App extends Vue {
    message: string = "Mr.zhai";
    dataList: Object[] = [];

    created(): void {
        this.getData()
    }

    mounted():void {
        window.addEventListener("scroll", this.handleScrollHeaderToggle, true);
    }

    getData():void {
        articleList()
            .then((data:any)=>{
                console.log(data);

                this.dataList = data.data.articles
            })
            .catch((err:any)=>{
                console.log(err);
            })
    }

    handleScrollHeaderToggle():void {
        let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
        if (scrollTop > 60) {
            (this.$refs.headerTypeList as HTMLElement).style.position = "fixed";
            (this.$refs.headerTypeList as HTMLElement).style.top = "0";
        } else {
            (this.$refs.headerTypeList as HTMLElement).style.position = "relative";
        }
    }
}
